}

.inputAndButtonContainer {
    display: flex; /* Используем Flexbox */
    align-items: center; /* Вертикально выравниваем элементы по центру */
    gap: 10px; /* Отступ между элементами */
}



.input {

        display: flex; /* Используем Flexbox для инпута и ошибки */
        flex-direction: column; /* Вертикальное расположение инпута и ошибки */

}

.input:focus {
}

.errorInput {
    border: 1px solid red;
}

.error {
    color: red; /* Цвет сообщения об ошибке */
    font-size: 12px; /* Размер шрифта ошибки */
}

.button {
    /* Вот некоторые базовые стили для кнопки */
    padding: 10px 15px;
    cursor: pointer;
    background-color: #007bff;
    color: white;
}

.button:focus {
}

.button:active {
}

.button:disabled {
    color: white;
    opacity: 0.5;
    cursor: not-allowed; /* Изменяем курсор на 'not-allowed', когда кнопка отключена */
}

.text {
    font-size: 14px;
    opacity: 0.7;
}

